.arcticmodal-overlay {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 1000; }

.arcticmodal-container {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
	overflow: auto;
	margin: 0;
	padding: 0;
	border: 0;
	border-collapse: collapse; }

*:first-child + html .arcticmodal-container {
	height: 100%; }

.arcticmodal-container_i {
	height: 100%;
	margin: 0 auto; }

.arcticmodal-container_i2 {
	padding: 24px;
	margin: 0;
	border: 0;
	vertical-align: middle; }

.arcticmodal-error {
	padding: 20px;
	border-radius: 10px;
	background: #000;
	color: #fff; }

.arcticmodal-loading {
	width: 80px;
	height: 80px;
	border-radius: 10px;
	background: #000 url(loading.gif) no-repeat 50% 50%; }


.box-modal {
	position: relative; }
.box-modal_close {
	position: absolute;
	right: -30px;
	top: 6px;
	font-size: 11px;
	line-height: 15px;
	color: #999;
	cursor: pointer;
	&:hover {
		color: #666; } }
